@import '../../../../../assets/css/theme';

.v3-pools-page {
  .position-flex {
    display: flex;
    > div {
      flex: 1;
    }
    > div:nth-child(1) {
      margin-right: 90px;
    }
  }
  .position-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-logo {
      position: relative;
      img {
        width: 20px;
        height: 20px;

        &:nth-child(2) {
          position: absolute;
          left: 14px;
          top: 0px;
        }
      }
    }
    &-info {
      margin-left: 25px;
      &-top {
        font-weight: bold;
        color: $white;
        font-size: 20px;

        span {
          font-size: 12px;
          opacity: 0.3;
          margin-left: 10px;
        }
      }
    }
    &-btn {
      > button.ant-btn {
        border-radius: 6px;
        width: 80px;
        /* 其他页面跳转样式被覆盖 */
        height: 30px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-size: 14px;
        line-height: 14px;
        &:nth-child(1) {
          color: $purple;
          background: $light-purple-btn;
          margin-right: 20px;
        }

        &:nth-child(2) {
          color: $white;
          background: $purple-btn;
        }
      }
    }
    &-jump-icon {
      width: 24px;
      height: 24px;
      margin-left: 20px;
    }
  }
  .position-liquidity {
    &-top {
      display: flex;
      justify-content: space-between;
    }
    &-bottom {
      font-size: 15px;
      color: $white;
      text-align: left;
    }
    &-symbol {
      font-weight: bold;
      color: $white;
      img {
        margin-right: 2px;
      }
    }
    &-radio {
      background-color: rgba($white, 0.1);
      color: $white;
      opacity: 0.7;
      font-size: 12px;
      padding: 2px 8px;
      border-radius: 2px;
    }
  }

  .position-rewards {
    &-content {
      display: flex;
      background: $color-input-bg;
      border-radius: 10px;
      padding: 12px 0;
    }
    &-box {
      width: 50%;
      padding: 0 20px;
      &:nth-child(1) {
        border-right: solid 1px rgba(255, 255, 255, 0.1);
      }
      img {
        width: 15px;
        height: 15px;
        vertical-align: sub;
      }
    }
  }

  .position-price {
    .position-inline {
      display: flex;
      align-items: center;
    }
    &-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &-current {
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      span:nth-child(1) {
        font-weight: 400;
        color: rgba($white, 0.3);
      }
      span:nth-child(2) {
        font-weight: bold;
        color: $white;
      }
    }
    &-symbol {
      font-size: 12px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $white;
      opacity: 0.5;
    }
    &-bottom {
      font-size: 14px;
      font-family: Helvetica-Bold, Helvetica;
      font-weight: bold;
      color: $white;
    }
    &-box {
      display: flex;
      background: $color-input-bg;
      border-radius: 10px;
      padding: 16px 0;
      > div {
        flex: 1;
        padding: 0 16px;
      }
      > div:nth-child(1) {
        border-right: solid 1px rgba($white, 0.1);
      }
    }
  }

  // common
  .position-info-text {
    font-size: 16px;
    color: rgba($white, 0.5);
    font-weight: 600;
  }
  .position-info-content {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    font-size: 18px;
    font-family: Helvetica-Bold, Helvetica;
    font-weight: bold;
    color: #ffffff;
    margin: 8px 0 12px;
    > button.ant-btn {
      width: 66px;
      height: 100%;
      background: $purple-btn;
      border-radius: 12px;
      padding: 2px 10px;
      font-size: 12px;
    }
  }
  .position-info-box {
    background: $color-input-bg;
    border-radius: 10px;
    padding: 12px;
    width: 50%;
    text-align: left;

    &:nth-child(2) {
      margin-left: 10px;
    }

    img {
      width: 15px;
      height: 15px;
      vertical-align: sub;
    }
  }

  .ant-skeleton:nth-child(1) {
    margin-bottom: 30px;
    li {
      height: 36px;
    }
  }
  .ant-skeleton:nth-child(2),
  .ant-skeleton:nth-child(3),
  .ant-skeleton:nth-child(6),
  .ant-skeleton:nth-child(7),
  .ant-skeleton:nth-child(9),
  .ant-skeleton:nth-child(10) {
    margin-bottom: 10px;
    li {
      height: 20px;
    }
  }
  .ant-skeleton:nth-child(4),
  .ant-skeleton:nth-child(5),
  .ant-skeleton:nth-child(11),
  .ant-skeleton:nth-child(12) {
    width: calc(50% - 5px);
    margin-bottom: 30px;
    span {
      width: 100%;
      height: 70px;
      border-radius: 10px;
    }
  }
  .ant-skeleton:nth-child(8) {
    margin-bottom: 30px;
    li {
      height: 70px;
    }
  }
  .position-mobile-btn {
    display: flex;
    margin: 20px 0 6px;
    > button {
      flex: 1;
    }
  }
}

@media screen and (max-width: 576px) {
  .v3-pools-page {
    .position-flex {
      display: block;
    }
    .position-flex > div:nth-child(1) {
      margin-right: 0;
    }
    .position-price-header {
      display: block;
    }
    .position-inline {
      margin-bottom: 10px;
    }
    .position-price-box {
      display: block;
      > div:nth-child(1) {
        margin-bottom: 12px;
      }
    }
    .position-info-text {
      font-size: 12px;
    }
  }
}
