@import '../../../../../assets/css/theme';

.info_range_line {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.info_tilte {
  font-size: 14px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $white;
  opacity: 0.7;
  > span {
    margin-right: 10px;
  }
}

.info_crt_price {
  font-size: 12px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: #ffffff;
  > span {
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    opacity: 0.5;
  }
}

.info_price_box {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background: #403858;
  border-radius: 10px;
  padding: 16px 0;
  margin-top: 16px;
  > div {
    flex: 1;
    padding-left: 16px;
    font-size: 16px;
    font-family: Helvetica-Bold, Helvetica;
    font-weight: bold;
    color: #ffffff;
    > span {
      font-size: 14px;
      font-family: PingFangSC-Semibold, PingFang SC;
      opacity: 0.5;
    }
    &:nth-child(2) {
      border-left: solid 1px rgba(255, 255, 255, 0.1);
    }
  }
}

@media screen and (max-width: 768px) {
  .info_range_line {
    display: block;
  }
  .info_crt_price {
    margin-top: 12px;
  }
  .info_price_box {
    display: block;
    > div:nth-child(1) {
      margin-bottom: 12px;
    }
  }
}
