$white: #fff;

.v3-pool-list {
  width: 90%;
  margin: 50px auto 0;
  max-width: 1200px;
  .pool-tilte {
    font-size: 14px;
    font-weight: bold;
    color: $white;
    line-height: 24px;
  }
  .pool-setting {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: rgba($white, 0.5);
    cursor: pointer;
    .ant-switch {
      margin-left: 12px;
    }
    .ant-switch-inner {
      background: #cccccc;
    }
    .ant-switch-checked {
      .ant-switch-inner {
        background: #8c51ff;
      }
    }
  }

  .v3-ques-png {
    width: 16px;
    height: 16px;
    margin-left: 10px;
    cursor: pointer;
  }

  .err-list {
    color: #bbb;
    // border: 1px solid #222;
    padding: 50px 0;
    text-align: center;
  }

  .wallet-link {
    display: inline-flex;
    align-items: center;
    padding: 12px 30px;
    color: #5915e1 !important;
    background: #c3a6fa;
    cursor: pointer;
    border-radius: 10px;

    > span {
      line-height: 16px;
      margin-left: 8px;
      margin-right: 38px;
      text-align: left;
    }

    > img {
      &:first-child {
        width: 17px;
        height: 17px;
      }

      &:last-child {
        width: 14px;
        height: 8px;
      }
    }
  }
  .sun-swap-empty-text {
    margin-top: 16px;
  }
}
