.v3-add-page {
  .v3-add-page-swap {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    > div {
      width: calc(50% - 40px);
    }
    > div:first-child {
      margin-right: 80px;
    }
  }
  .v3-add-page-swap-inline {
    > div:first-child {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      > div {
        width: calc(50% - 40px);
      }
      > div:nth-child(1) {
        margin-right: 80px;
      }
    }
  }
  &-header {
    color: #ffffff;
    font-size: 15px;
    margin-bottom: 15px;
    a,
    a:link,
    a:visited {
      color: #ffffff;
    }
  }
  .setting-line {
    margin: 15px 0;
    > div:nth-child(1) {
      color: #ffffff;
      opacity: 0.23;
      font-size: 14px;
    }
    .sun-swap-link-3 {
      color: #c1a0ff;
      font-size: 12px;
      cursor: pointer;
    }
  }
  .current-price {
    line-height: 14px;
    &-label {
      color: rgba(255, 255, 255, 0.3);
      font-size: 12px;
    }
    &-text {
      font-weight: bold;
      color: #ffffff;
      font-size: 12px;
    }
  }
  .add-liquidity-error-orange {
    font-size: 15px;
    font-family: Helvetica sans-serif;
    color: #ff8e15;
    line-height: 18px;
    margin-top: 15px;
  }
}

@media screen and (max-width: 768px) {
  .v3-add-page {
    .v3-add-page-swap {
      display: block;
      > div {
        width: 100%;
        margin-right: 0;
      }
    }

    .v3-add-page-swap-inline {
      > div:first-child {
        display: block;
        > div {
          width: 100%;
          margin-right: 0;
        }
      }
    }
  }
}
