.current-price-wrap {
  margin-bottom: 20px;
}
.current-price-desc {
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #ffffff;
  background: rgba(193, 160, 255, 0.2);
  padding: 12px 14px;
  border-radius: 10px;
  line-height: 22px;
}
.current-token-price {
  font-size: 14px;
  font-family: Helvetica;
  color: rgba(255, 255, 255, 0.7);
  line-height: 18px;
  margin: 14px 0 12px;
  display: flex;
  align-items: center;
  .v3-coin-logo-medium {
    width: 20px;
    height: 20px;
  }
}
.current-price-symbol {
  font-size: 15px;
  font-weight: bold;
  color: #ffffff;
  margin-right: 6px;
}

#root .v3-current-price-input {
  width: 100%;
  background: #403858;
  border: 1px solid transparent;
  box-shadow: 0 2px 14px 0 rgba(113, 95, 222, 0.1);
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  &::placeholder {
    color: rgba(255, 255, 255, 0.3);
  }
}
#root .v3-current-price-input:focus {
  border: 1px solid #c1a0ff;
}

.v3-price-suffix {
  display: flex;
  align-items: center;
  &-img {
    width: 20px;
    height: 20px;
    margin-right: 6px;
  }
  &-symbol {
    font-size: 20px;
    font-weight: bold;
    color: #ffffff;
  }
}
.mobile-content {
  .current-price-desc {
    font-size: 12px;
  }
}
