$white: #ffffff;

.v3-add-page {
  .swap-block {
    .lock-block {
      width: 100%;
      padding: 0 10px;
      height: 100%;
      background: #403858;
      border-radius: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      padding: 16px 16px;
      > span {
        color: $white;
        font-size: 12px;
        opacity: 0.7;
        margin-top: 6px;
        text-align: center;
      }
    }
    .input-bottom-usd {
      font-size: 14px;
      font-family: Helvetica;
      color: $white;
      opacity: 0.7;
    }
  }
}
