$border-color: rgba(255, 255, 255, 0.3);
$active-bg: #722fff;
$text-color: #fff;
$desc-color: rgba(255, 255, 255, 0.5);
$no-create-color: rgba(255, 255, 255, 0.7);

.fee-selector {
  &-item {
    border: 1px solid $border-color;
    color: $text-color;
    border-radius: 10px;
    padding: 14px 16px;
    cursor: pointer;
    height: 100%;
    //min-height: 116px;
    display: flex;
    flex-direction: column;
    &-active {
      border: 1px solid transparent;
      background: $active-bg;
      cursor: default;
    }
    &-percent {
      font-size: 20px;
      font-weight: bold;
      font-family: Helvetica-Bold, Helvetica;
      color: $text-color;
      line-height: 24px;
      margin-bottom: 6px;
    }
    &-desc {
      color: $desc-color;
      line-height: 18px;
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      margin-bottom: 5px;
    }
    &-no-create {
      font-size: 14px;
      color: $no-create-color;
      line-height: 18px;
      font-weight: bold;
      margin-top: auto;
    }
  }
}
.mobile-content {
  .fee-selector-item {
    padding: 5px;
    &-percent {
      font-size: 16px;
    }
    &-desc,
    &-no-create {
      font-size: 12px;
    }
  }
}
