.v3-remove-modal {
  .ant-modal-content {
    padding: 25px 20px;
    border-radius: 20px;
  }
  .ant-modal-body {
    padding: 0;
    text-align: center;
  }
  .title {
    margin: 40px 0;
    font-weight: 500;
    color: #333;
    font-family: PingFangSC-Regular;

    span {
      margin-right: 3px;

      &:last-child {
        margin-right: 0;
      }
    }

    &.big {
      margin: 0 0 5px;
      font-size: 20px;
    }

    &.normal {
      font-size: 15px;
      margin: 0 0 12.5px;
    }

    &.green {
      font-family: PingFangSC-Medium;
      font-size: 25px;
      color: #16c378;
      margin: 30px 0 15px;
    }
  }
  .title.big {
    font-size: 15px;
    font-family: Helvetica;
    color: #333333;
    line-height: 18px;
    text-align: center;
  }
  .amount {
    margin: 20px 0 20px;
    // border-bottom: 1px solid hsla(0, 0%, 84.7%, 0.5);
    font-size: 20px;
    font-family: Helvetica-Bold, Helvetica;
    font-weight: bold;
    color: #333333;
    line-height: 24px;
    .add-token img {
      width: 15px;
      height: 15px;
    }
  }
  .plus {
    font-size: 20px;
    color: #333;
    margin: 0 4px;
    opacity: 0.5;
  }

  .price-box {
    margin-bottom: 30px;
    .param {
      justify-content: space-between;
    }
    .price-value {
      > img {
        width: 30px;
        height: 30px;
        margin: 0;
        &:nth-child(2) {
          margin-left: -6px;
        }
      }
      > div {
        font-size: 20px;
        font-family: Helvetica-Bold, Helvetica;
        font-weight: bold;
        color: #333333;
        vertical-align: middle;
        margin-left: 10px;
      }
      > p {
        font-size: 12px;
        font-family: Helvetica;
        color: #333333;
        text-align: right;
      }
    }
  }
  .endTip {
    opacity: 0.8;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #999999;
    margin-top: 17.5px;
    display: block;
  }
}
