@import '../../../../../assets/css/theme';

.v3-pool-list {
  .position {
    border-radius: 10px;
    margin-bottom: 10px;
    background: $color-table-bg;
    position: relative;
    cursor: pointer;
    &-header {
      display: flex;
      justify-content: space-between;
      padding: 10px;
      &-logo {
        position: relative;
        img {
          width: 20px;
          height: 20px;

          &:nth-child(2) {
            position: absolute;
            left: 14px;
            top: 0px;
          }
        }
      }
      &-info {
        margin-left: 25px;
        &-top {
          font-weight: bold;
          font-family: PingFangSC-Semibold, PingFang SC;
          font-weight: 600;
          color: $color-a;
          font-size: 14px;

          span {
            font-size: 14px;
            color: $white;
            opacity: 0.3;
            margin-left: 10px;
          }
        }
      }
    }
    &-content {
      padding: 0 10px;
      &-title {
        font-size: 12px;
        color: $white;
        font-family: PingFangSC-Regular, PingFang SC;
        margin-bottom: 10px;
        > span:nth-child(1) {
          opacity: 0.5;
          font-weight: 400;
        }
        > span:nth-child(1) {
          font-weight: 600;
        }
      }
    }
    &-button {
      border-top: solid 1px rgba($color: $white, $alpha: 0.05);
      > div {
        width: 100%;
        font-size: 14px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: $color-a;
        padding: 8px 0;
        text-align: center;
        &:first-child {
          border-right: solid 1px rgba($color: $color-a, $alpha: 0.05);
        }
      }
      .disable {
        color: rgba($color: $white, $alpha: 0.5);
        pointer-events: none;
        cursor: not-allowed;
      }
    }
  }
}
