.v3-section-title {
  font-size: 16px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: #ffffff;
  font-weight: bold;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &-bottom {
    margin-bottom: 10px;
  }
}

@media screen and (max-width: 576px) {
  .v3-section-title {
    font-size: 14px;
  }
}
